import React, { useState } from "react";
import "../assets/Contact.css";
import db from "../utils/connexion";
import { collection, addDoc } from "firebase/firestore"
import {BrowserRouter as Router,Link} from "react-router-dom";




function Contact() {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [lastname, setLastname] = useState("");
    const [message, setMessage] = useState("");

    const newMessage = async () => {
        await addDoc(collection(db, "form"), {
            name,
            lastname,
            email,
            message
        }).then((res) => {
            alert("Message has been submited");
        }).catch((error) => {
            alert(error.message);
        });
        
    };

    return (

        <div className="contact">
            <div className="container containerContact">
                <div className="contact-box ">
                    <div className="contact-info">
                        <h3 className="titler">Get in touch :</h3>
                        <div className="information-wrap">

                            <div className="information">
                                <div className="contact-icon">
                                    <i className="bi bi-envelope-plus"></i>
                                </div>
                                <p className="info-text pt-3 fw-bolder">wbelegchour@gmail.com</p>
                            </div>

                            <div className="information">
                                <div className="contact-icon">
                                    <i className="bi bi-telephone-fill"></i>
                                </div>
                                <p className="info-text pt-3 fw-bolder">+212 61793052*</p>
                            </div>
                        </div>
                    </div>

                    <div className="contact-form text-center bg-black2 text-white p-5"
                    >
                        <h3 className="title pb-3 text-center">Email me <i className="bi bi-envelope-plus"></i></h3>
                        <div className="row">
                            <input
                                type="text"
                                className="contact-input"
                                placeholder="First Name"
                                onChange={(e) => setName(e.target.value)}
                            />
                            <input
                                type="text"
                                className="contact-input"
                                placeholder="Last Name"
                                onChange={(e) => setLastname(e.target.value)}
                            />
                        </div>

                        <div className="row">
                            <input
                                type="email"
                                className="contact-input"
                                placeholder="Email"
                                onChange={(e) => setEmail(e.target.value)}
                            />
                        </div>

                        <div className="row">
                            <textarea
                                type="message"
                                className="contact-input textarea"
                                placeholder="Message"
                                onChange={(e) => setMessage(e.target.value)}
                            ></textarea>
                        </div>
            <Link to="/"><button
                            onClick={newMessage}
                            className="btn1 btn btn-outline-danger mt-3 p-3">Submit</button></Link>
                    </div>
                </div>
            </div>
        </div>
    )
}



export default Contact;